import React, {useState, useEffect} from 'react'
import introIllustration from '../img/intro-illustration_qneuer.svg';

function HeadBottom() {
  const [hello, setHello] = useState('')
  const [para, setPara] = useState('')

  let title = "Image Annotation",
  i = 0,
  now = ''

  let description = "Data is the fuel to AI and for the same . . . . AI starts with quality data with good annotation, High quality training and validation data for AI applications",
  p = true
  useEffect(() => {
    const typeW = (text, set) => {
      const typewritter = setInterval(() => {
        now = now + text[i]
        set(now)
        i++
        let textLength = text.length
        if (i >= textLength) {
          if (p) {
            typeW(description, setPara)
            p = false
          }
          clearInterval(typewritter)
          i = 0
          now = ''
        }
      }, 50)
    }
    typeW(title, setHello)
  }, [])
  return (
    <div className="head-bottom container">
      <div className="left-info">
        <h1>{hello}</h1>
        <p>{para}</p>
      </div>
      <img src={introIllustration} alt="intro-illustration" />
    </div>
  )
}

export default HeadBottom
