import React from 'react'
import introIllustration from '../img/intro-illustration_qneuer.svg';

function Section2() {
  return (
    <section className="section2 container">
      <img src={introIllustration} alt="intro-illustration" />
      <div className="right-info">
        <h2>Construct your Machine Learning pipeline with Powerful APIs</h2>
        <p>Stream data into Labelbox and push labeled data into training environments. Connect your ML models to supercharge labeling productivity and orchestrate active learning. Labelbox is API-first so you can use it as infrastructure to scale up.</p>
      </div>
    </section>
  )
}

export default Section2
