import React, {useState, useEffect} from 'react';
import './App.css';

import Head from './components/Head'
import HeadBottom from './components/HeadBottom';
import Section1 from './components/Section1'
import Section2 from './components/Section2'
import Footer from './components/Footer'


function App() {

  useEffect(() => {
    document.title = "Edunomics"
  }, [])

  const [cursor, setCursor] = useState({
    x: 400,
    y: 400,
    width: 30 + 'px',
    height: 30 + 'px',
    transition: '.4s'
  })
  const [cursorColor, setCursorColor] = useState('#fc0363')
  const [locked, setLocked] = useState(false)

  const changeColor = (e) => {
    setCursorColor(e.target.getAttribute('data-cursor-color'))
    // setCursorTransition('.2s')
    if (e.target.getAttribute('data-fixed-cursor') === 'true') {
      let navIcon = document.getElementById(e.target.id)
      console.log(navIcon.getBoundingClientRect());
      let navIconCoor = navIcon.getBoundingClientRect()
      let cursorSize = 0
      if (navIconCoor.width >= navIconCoor.height) {
        cursorSize += navIconCoor.width
      } else {
        cursorSize += navIconCoor.height
      }
      setLocked(true)
      setCursor({x: navIconCoor.left + (navIconCoor.width / 2), y: navIconCoor.top + (navIconCoor.height / 2), width: cursorSize, height: cursorSize, transition: '.4s'})
    }
    
  } 
  const mouseMove = (e) => {
    return locked ? false : setCursor({x: e.clientX, y: e.clientY})
  }
  const changeColorOut = () => {
    setCursorColor('#fc0363')    
    setLocked(false)
  } 

  return (
    <div className="App" onMouseMove={mouseMove} >
      <div className="cursor" style={{top: cursor.y + 'px', left: cursor.x  + 'px', borderColor: cursorColor, width: cursor.width, height: cursor.height}}></div>
      <header className="App-header">
        <Head changeColor={changeColor} changeColorOut={changeColorOut} />
        <HeadBottom />
      </header>
      <Section1 />
      <Section2 />
      <Footer changeColor={changeColor} changeColorOut={changeColorOut} />
    </div>
  );
}

export default App;
