import React from 'react'
import logo from '../img/logo.svg'

function Head(props) {
  return (
    <div className="head">
      <div className="container">
        <img src={logo} alt="Learn english"/>
        <nav>
          <ul>
            <li><a href="#"
              data-cursor-color="#2c3e50" 
              onMouseEnter={props.changeColor} 
              onMouseLeave={props.changeColorOut}
            >Login</a></li>
            <li><a href="#"
              data-cursor-color="#2c3e50" 
              onMouseEnter={props.changeColor} 
              onMouseLeave={props.changeColorOut}
            >Signup</a></li>
          </ul>
        </nav>
      </div>
    </div>
  )
}


export default Head
