import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookF } from "@fortawesome/free-brands-svg-icons"
import { faInstagram } from "@fortawesome/free-brands-svg-icons"
import { faLinkedin } from "@fortawesome/free-brands-svg-icons"
import { faTwitter } from "@fortawesome/free-brands-svg-icons"

export default function Footer(props) {
  return (
    <footer className="container">
      <div className="footer-top">
        <p>AN INITIATIVE BY EDUNOMICS.IN TO MAKE AI ACCESSIBLE</p>
        <div className="social-links">
          <p>VISIT <a href="http://www.edunomics.in"
            data-cursor-color="#2c3e50" 
            onMouseEnter={props.changeColor} 
            onMouseLeave={props.changeColorOut}
          >WWW.EDUNOMICS.IN</a></p>
          <ul>
            <li><a href="#" id="facebook"
              data-cursor-color="#4267b2" 
              onMouseEnter={props.changeColor} 
              onMouseLeave={props.changeColorOut}
              data-fixed-cursor='true'
            ><FontAwesomeIcon icon={faFacebookF} /></a></li>
            <li><a href="#" id="instagram"
              data-cursor-color="#e1306c" 
              onMouseEnter={props.changeColor} 
              onMouseLeave={props.changeColorOut}
              data-fixed-cursor='true'
            ><FontAwesomeIcon icon={faInstagram} /></a></li>
            <li><a href="#" id="linkedin"
              data-cursor-color="#2867B2" 
              onMouseEnter={props.changeColor} 
              onMouseLeave={props.changeColorOut}
              data-fixed-cursor='true'
            ><FontAwesomeIcon icon={faLinkedin} /></a></li>
            <li><a href="#" id="twitter"
              data-cursor-color="#1da1f2" 
              onMouseEnter={props.changeColor} 
              onMouseLeave={props.changeColorOut}
              data-fixed-cursor='true'
            ><FontAwesomeIcon icon={faTwitter} /></a></li>
          </ul>
        </div>
      </div>
      <p className="copyright">&copy; Edunomics 2020. All Rights Reserved</p>
    </footer>
  )
}
