import React from 'react'
import icon1 from '../img/icon1.svg'
import icon2 from '../img/icon2.svg'
import icon3 from '../img/icon3.svg'

function Section1() {
  return (
    <section className="section1 container">
      <ul>
        <li>
          <img src={icon1} alt="headphone icon"/>
          <p>Annotation - Let Edunomics bring the best of DATA Platform for AI</p>
        </li>
        <li>
          <img src={icon2} alt="video playlist icon"/>
          <p>Developed by the best brains of world with intent to make AI accessible</p>
        </li>
        <li>
          <img src={icon3} alt="letter icon"/>
          <p>Our tool is always free for Academic research</p>
        </li>
      </ul>
    </section>
  )
}

export default Section1
